  .dtable-ui-mb-editor-popover {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
    background-color: #f5f5f5;
    z-index: 9;
  }

  .dtable-ui-mb-editor-header {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 50px;
    display: flex;
    align-items: center;
    background-color: #fff;
  }

  .dtable-ui-mb-editor-header .editor-header-left-content {
    min-width: 50px;
    padding: 13px 16px;
    font-size: 14px;
    color: #888;
  }

  .dtable-ui-mb-editor-header .editor-header-title {
    flex: 1;
    font-size: 18px;
    text-align: center;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
  }

  .dtable-ui-mb-editor-header .editor-header-right-content {
    min-width: 50px;
    padding: 13px 16px;
    font-size: 14px;
    color: #888;
  }

  .dtable-ui-mb-editor-body {
    flex: 1;
    display: flex;
    flex-direction: column;
    margin-top: 50px;
    overflow: auto;
  }
